<script>
	import { afterUpdate } from 'svelte'
	import TaskTable from './TaskTable.svelte'
	const weekdays = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
	let name = 'world'
	let date = new Date(Date.now())
	$: dateString = `${weekdays[date.getDay()]} ${date.getMonth()+1}/${date.getDate()}`
	$: storageString = `task-tracker-${date.getMonth()+1}-${date.getDate()}`
	$: tasks = (localStorage.getItem(storageString)) ? JSON.parse(localStorage.getItem(storageString)) : [{client: 'eko',task: 'Make a spreadsheet',	startTime: '13:04',	endTime: '13:34'}]
	
	function clearTasks() {
		tasks = []
	}
	
	afterUpdate(() => {
		console.log(`I'm gonna store the following string in memory at ${storageString}:\n${JSON.stringify(tasks)}`);
		localStorage.setItem(storageString, JSON.stringify(tasks))
	})

	const dayInMS = 1000*60*60*24
	function changeDate(change) {
		return date.getTime() + change*dayInMS
	}

	function nextDay() {
		date = new Date(changeDate(1))
		console.log(date, dateString)
	}
	function previousDay() {
		date = new Date(changeDate(-1))
		console.log(date, dateString)
	}
</script>

<header>
	<h1>Task Tracker</h1>
	<div class="date-container">
		<button on:click={previousDay}>â¬…</button>
		<h2>{ dateString }</h2>
		<button on:click={nextDay}>âž¡</button>
	</div>
	<button class="btn_clear-task" on:click={clearTasks}>Clear Tasks</button>
</header>
<main>
	<TaskTable bind:date bind:tasks />
</main>

