<script>
	export let date = ''
	export let tasks = []
	let newTask = createNewTask()
	
	function now() {
		let now = new Date(Date.now())
		return `${now.getHours()}:${now.getMinutes().toString().padStart(2, '0')}`
	}

	function nowPlusMins(mins) {
		let hrs = 0
		let now = new Date(Date.now())
		if (now.getMinutes()+15 > 60) {
			hrs = 1
			mins = now.getMinutes()+15%60
		} else {
			hrs = 0
			mins = now.getMinutes()+15
		}
		return `${now.getHours()+hrs}:${mins.toString().padStart(2,'0')}`
	}
	
	function addDateToTime(date, time) {
		return new Date(date.getYear() +' '+date.getMonth()+' '+ date.getDate() +' '+ time)
	}
	
	function formatTime(timeStr) {
		let timeDate = addDateToTime(date, timeStr)
		return `${(timeDate.getHours() <= 12) ? timeDate.getHours() : timeDate.getHours() - 12 }:${timeDate.getMinutes().toString().padStart(2, '0')} ${(timeDate.getHours() < 12) ? 'am' : 'pm'}`
	}
	
	function calcDuration(start, end) {
		let startT = addDateToTime(date, start)
		let endT = addDateToTime(date, end)
		let dur = endT - startT
		return `${Math.floor(dur/1000/60/60)}h ${dur/1000/60%60}m`
	}
	
	function createNewTask() {
		return {
			client: '',
			task: '',
			startTime: now(),
			endTime: nowPlusMins(15)
		}
	}
	
	function addTask() {
		tasks = [...tasks, newTask]
		newTask = createNewTask()
		document.getElementById('input_client').focus()
	}
	
	function handleFormKeyUp(e) {
		if (e.key === 'Enter') {
			addTask()
		}
	}
	
	function delTask(e) {
		let tasksCopy = [...tasks]
		let i = parseInt(e.target.dataset.id)
		tasks = [...tasksCopy.slice(0, i), ...tasksCopy.slice(i+1)]
	}
</script>

<table>
	<tbody>
		<tr>
			<th class='l'>Client</th>
			<th class="l">Task</th>
			<th>Start</th>
			<th>End</th>
			<th>Duration</th>
		</tr>
		{#each tasks as task, i (task.task + task.startTime)}
		<tr>
			<td>{ task.client }</td>
			<td>{ task.task }</td>
			<td class='c'>{ formatTime(task.startTime) }</td>
			<td class='c'>{ formatTime(task.endTime) }</td>
			<td class='c'>{ calcDuration(task.startTime, task.endTime) }</td>
						<button class="btn_del" data-id={i} on:click={delTask}>ðŸ—‘</button>
		</tr>
		{/each}
		<tr>
			<td>
				<input id='input_client' type='text' bind:value={newTask.client} placeholder="Client, LLC"
							 on:keyup={handleFormKeyUp} />
			</td>
			<td>
				<input class='input_task-name' type='text'
							 bind:value={newTask.task} placeholder="Enter a new task"
							 on:keyup={handleFormKeyUp} />
			</td>
			<td class='c'>
				<input type='time' bind:value={newTask.startTime}
							 on:keyup={handleFormKeyUp} />
			</td>
			<td class='c'>
				<input type='time' bind:value={newTask.endTime}
							 on:keyup={handleFormKeyUp} />
			</td>
			<td class='c'>
				<button class="btn_add" on:click={addTask}>Add Task</button>
			</td>
		</tr>
	</tbody>
</table>

